  private async btn_test2() {
    let map = this.$refs.gisViewer as any;
    var points = [];
    var x = 121.43;
    var y = 31.15;
    for (var i = 0; i < 20; i++) {
      var x1 = x + (Math.random() * 2 - 1) / 5;
      var y1 = y + (Math.random() * 2 - 1) / 5;
      var value = Math.floor(1000 * Math.random() + 1);
      var a = i % 2 == 0 ? "1" : "0";
      points.push({
        geometry: { x: x1, y: y1 },
        fields: { desc: "", totalSpace: value, type: a }
      });
    }
    var json = {
      points: points,
      options: {
        field: "totalSpace",
        radius: "20",
        colors: [
          "rgb(25, 154, 114)",
          "rgb(61, 192, 67)",
          "rgb(206, 199, 25)",
          "rgb(225, 145, 27)",
          "rgb(246, 64, 64)"
        ],
        maxValue: 1000,
        minValue: 1,
        zoom: 17,
        renderer: {
          type: "simple",
          symbol: {
            type: "esriPMS",
            url: "assets/image/Anchor.png",
            width: 64,
            height: 66,
            yoffset: 16
          }
        }
      }
    };
    map.addHeatMap(json);
    map.addOverlays({
      type: "police",
      defaultSymbol: {
        //symbol for 2d
        type: "point-2d",
        // primitive: "square",
        url: "assets/image/Anchor.png",
