        //symbol for 3d
        //type: "point-3d",
        //primitive: "cube",
        //color: "red",
        //size: 20000,
        //anchor: "bottom",
      },
      defaultZooms:[10,20],
      overlays: [
        {
          id: "test001",
          geometry: { x: 121.418924, y: 31.157101 },
          fields: { name: "2", featureid: "0002" }
        },
        {
          id: "test002",
          geometry: { x: 121.318924, y: 31.157101 },
          fields: { name: "3", featureid: "0003" }
        },
        {
          id: "test003",
          geometry: { x: 121.418924, y: 31.257101 },
          fields: { name: "4", featureid: "0001" }
        }
      ],
      showPopup: true,
      autoPopup: false,
      defaultInfoTemplate: {
        title: "1212",
        content: "<div>name:{name}<br/><button>{name}</button></div>"
      },
      defaultButtons: [{ label: "", type: "confirmAlarm" }]
    });
    (this.$refs.gisViewer as any).showLayer({ type: "traffic" });
    (this.$refs.gisViewer as any).showDistrictMask({
      name: "",
      showMask: true
    });
  }
